
    import {apiService, apiUrl} from '@/api'
	import LoadMore from 'vux/src/components/load-more/index.vue'

	export default {
		data() {
			return {
				articleId: 0,
				articleInfo: {},
				isShowPage: false,
				comments: [],
				commentParams: {
					pageSize: 0,
					pageNum: 0
				},
				isLoadComment: false,
				isMoreComment:true
			}
		},
		components: {
			LoadMore
		},
		methods:{
			getComment(){//加载评论
				var vm =this;
				vm.isLoadComment = true;
				apiService.requestGet(apiUrl.baseUrl + "judge/getJudge/"+vm.commentParams.pageNum+"/"+vm.commentParams.pageSize).then((res)=>{
					if(res.data.length != 0){
						for(var item of res.data){
							vm.comments.push(item);
						  }
						  vm.isMoreComment =true;
					}else {
						vm.isMoreComment = false;
					}
					vm.isLoadComment = false
					
				}, (res)=>{
					vm.isMoreComment = false;
					vm.isLoadComment = false
				});
			},
			getMoreComment(){
				var vm = this;
				vm.commentParams.pageNum += 1;
				vm.getComment();
			}
		},
		created() {
			var vm = this;
			vm.articleId = this.$route.query.id;
			apiService.requestGet(apiUrl.baseUrl + "article/getArticleDetail/" + vm.articleId).then((res)=> {
				vm.articleInfo = res.data;
				vm.getComment();
				vm.isShowPage= true;
			}, (res)=>{
				console.log("服务器错误");
			}).catch((e)=>{
				console.log(e);
			});
		},
		computed: {

		},
	}



