
    import {apiService, apiUrl} from '@/api'
	import LoadMore from 'vux/src/components/load-more/index.vue'

	export default {
		components: {
			LoadMore
		},
		data() {
			return {
				dataList: [],
				paramList: {//文章列表
					pageNum: 0,
					pageSize: 2
				},
				isLoad:false,
				isNext: true
			}
		},
		created() {
			var vm = this;
			vm.getArticleList();
		},
		methods: {
			getArticleList() {//获取文章列表
				var vm = this;
				var url = apiUrl.baseUrl + "article/getArticle/"+ vm.paramList.pageNum+ "/" + vm.paramList.pageSize;
				apiService.requestGet(url).then((res)=> {
					if(res.data.length != 0){
						for(var item of res.data){
							vm.dataList.push(item);
						}
						vm.isNext = true;
					}else {
						vm.isNext =false;
					}
					vm.isLoad = false;
					 
				},(res)=> {
					console.log("数据错误");
					vm.isNext =false;
					vm.isLoad = false;
				})
			},
			getArticleListMore(){
				var vm =this;
				vm.paramList.pageNum = vm.paramList.pageNum +1;
				vm.getArticleList();
				vm.isLoad= true;
			}

		},
		computed: {

		},
		watch: {

		}
	}



